/* ------------------------------------
  # GENERAL
-------------------------------------- */

$base-img-path: '../../images/';

$font-weight-normal: 400;
$font-weight-bolden: 600;

// Required if using the background helpers
$overlay-background-colour: rgba(0, 0, 0, 0.5);
$overlay-gradient-colour: "{{ Linear RGBA Gradient }}";

/*----------------------------------------
    # Media Queries
----------------------------------------*/
// Usage: @media only screen and (max-width: $large) { ... }
$large: 1300px;
$medium-large: 1100px;
$medium: 1000px;
$largeTablet: 920px;
$tablet: 767px;
$phablet: 575px;
$mobile: 480px;
$smallmobile: 375px;


@font-face {
  font-family: VideoJS;
  src: url(data:application/font-woff;charset=utf-8;base64,) format("woff"), url(data:application/x-font-ttf;charset=utf-8;base64,) format("truetype");
  font-weight: 400;
  font-style: normal
}
@font-face {
  font-family: 'leikoregular';
  src: url('fonts/leiko-regular-webfont.woff2') format('woff2'),
  url('fonts/leiko-regular-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;

}

/*----------------------------------------
  # Typography
----------------------------------------*/
@import url('https://fonts.googleapis.com/css?family=Tinos:400,500,600,700,800&display=swap');



$headings-font-family: 'leikoregular', sans-serif;
$base-font-size: 16px;

// H1
$h1-font-size: rem(55px);
$h1-line-height: 1.2;
$h1-font-weight: 300;
$h1-letter-spacing: 3px;

// H2
$h2-font-size: rem(40px);
$h2-line-height: 1.2;
$h2-font-weight: 300;
$h2-letter-spacing: 3px;

// h3
$h3-font-size: rem(28px);
$h3-line-height: 1.2;
$h3-font-weight: 300;
$h3-letter-spacing: 3px;

// h4
$h4-font-size: rem(18px);
$h4-line-height: 1.2;
$h4-font-weight: 300;
$h4-letter-spacing: 3px;

// h5
$h5-font-size: rem(16px);
$h5-line-height: 1.2;
$h5-font-weight: 300;
$h5-letter-spacing: 3px;

// h6
$h6-font-size: rem(8px);
$h6-line-height: 1.2;
$h6-font-weight: 300;

// Paragraph
$paragraph-size: rem(18px);
$heading-color: #133064;
$paragraph-color: #133064;
$paragraph-line-height: 1.4;
$paragraph-font-family:  'Tinos', sans-serif;

$small-paragraph-size: rem(14px);


/*----------------------------------------
  # Colours
----------------------------------------*/

$primary: #f1f1f1;
$lightgrey: #f1f1f1;

$colors: (
        beauty-bush: #ecc3bd,
        spring-rain: #aeccb5,
        sisal: #d7cdc3,

        monte-carlo: #74CAC9,
        oracle: #3D7D7C,
        apricot: #F18366,
        medium-carmine: #A44C35,
        alabaster: #F8F8F8,

        tropicalrainforest: #007262,
        timbergreen: #1C4A48,
        palmgreen: #1C2F28,
        springwood: #F2F1E7,
        red: #F24E4E,
        white: #FFFFFF,
        purple: #9D5CFF,
        yellow: #F2C94E,
        orange: #FFB800,
        blue: #112E66,
        lightblue: #F1F6FF,
        black: #2b2b2b,
        trublack: #000000,
        lightblack: #474747,
        lightgrey: #9ea9a9,
        grey: #838689,
        darkgrey: #615d5d,
        darkblue: #0E3B93,
        error: (
                500: #F24E4E,
        ),
        success: (
                500: #56DEA3,
        ),
);



