main {
  background-color: #FFFFFF;
}

body, main {
  overflow-y: scroll;
  /* has to be scroll, not auto */
  -webkit-overflow-scrolling: touch;
}

a.abs {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.grecaptcha-badge {
  opacity: 0;
}

figure {
  margin: 0 auto;
  line-height: 0;
  text-align: center;
}

div.mfp-figure {
  background-color: transparent;
  padding: 0 2rem;
}

div.mfp-figure:after {
  box-shadow: none;
  border: none;
  background: transparent;
}

.mfp-with-zoom .mfp-container,
.mfp-with-zoom.mfp-bg {
  opacity: 0;
  -webkit-backface-visibility: hidden;
  /* ideally, transition speed should match zoom duration */
  -webkit-transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.mfp-with-zoom.mfp-ready .mfp-container {
  opacity: 1;
}

.mfp-with-zoom.mfp-ready.mfp-bg {
  opacity: 0.8;
}

.mfp-with-zoom.mfp-removing .mfp-container,
.mfp-with-zoom.mfp-removing.mfp-bg {
  opacity: 0;
}

.white-popup-block {
  background: #FFF;
  padding: 20px 30px;
  text-align: left;
  max-width: 650px;
  margin: 40px auto;
  position: relative;
}

.button {
  display: inline-block;
  margin: 1rem 0 0 0;
  font-size: 1.2rem;
  font-family: "leikoregular", sans-serif;
  border-bottom: 1px solid transparent;
  transition: border ease-in 200ms;
}

.button:focus {
  outline: none;
}

.button:hover {
  text-decoration: none;
  border-bottom: 1px solid #FFFFFF;
  transition: border ease-in 200ms;
}

.button.standard {
  display: inline-block;
}

.button.standard:after {
  display: inline;
  font-family: 'Font Awesome 5 Free';
  content: '  \f054';
  font-weight: 900;
  padding: 0 5px 5px 0;
}

.button.call-to-action {
  display: inline-block;
  color: #FFFFFF;
  background-size: 100% 100%;
  text-decoration: none;
  padding: 0.6rem 2rem;
  cursor: pointer;
  background: #112E66;
  text-align: center;
  transition: all ease-in 200ms;
}

.button.call-to-action:hover {
  background: #0E3B93;
  color: #FFFFFF;
  transition: all ease-in 200ms;
}

.button.call-to-action:after {
  content: "";
}

/* ------------------------------------
	# 05-COMPONENTS
-------------------------------------- */
header.header {
  position: sticky;
  top: 0;
  z-index: 5;
  background-color: #FFFFFF;
  grid-template-columns: 1fr 150px 1fr;
  grid-template-rows: 1fr;
}

header.header .grid-layout {
  align-items: stretch;
}

header.header .grid-layout nav {
  grid-row: 1;
  display: inline-flex;
  justify-content: space-around;
}

header.header .grid-layout nav a {
  text-align: center;
  border-bottom: 5px solid transparent;
  transition: border ease-in 200ms;
  display: inherit;
  align-items: center;
  font-family: "leikoregular", sans-serif;
}

header.header .grid-layout nav a p {
  margin: 0;
}

header.header .grid-layout nav a:hover {
  text-decoration: none;
  transition: border ease-in 200ms;
  border-bottom: 5px solid #FFB800;
}

header.header .grid-layout a.logo {
  grid-row: 1;
  text-align: center;
}

header.header .grid-layout a.logo:hover {
  text-decoration: none;
}

header.header .grid-layout a.logo .fas {
  background: #F1F6FF;
  margin: 2rem auto;
  padding: 1rem 2rem;
}

#hero {
  grid-template-rows: 1fr 50px;
}

#hero #my-video {
  grid-row: 1 / span 2;
  grid-column: 1 / span 14;
  object-fit: cover;
  width: 100%;
  height: unset;
  max-height: 85vh;
  overflow: hidden;
}

#hero #my-video video {
